<script>
export default {
  data() {
    return {
      fileList: [],
      tableData: [
        {
          id: "1",
          moduleName: "",
          competitionTime: "1",
          score: "20",
        },
        {
          id: "2",
          moduleName: "",
          competitionTime: "1",
          score: "40",
        },
        {
          id: "3",
          moduleName: "",
          competitionTime: "2",
          score: "40",
        },
      ],
      runBat: 1,
      baseURL: this.$axios.defaults.baseURL,
      id: localStorage.getItem("id"),
      headers: {
        Authorization: "Bearer " + localStorage.getItem("token"),
      },
    };
  },
  mounted() {
    // this.ifHaveToken();
  },
  methods: {
    //
    DownLoad() {
      let random = Math.random() * 150;
      random = random * 10;
      let loading = this.$loading({ text: "" });
      setTimeout(() => {
        loading.close();
        this.$message.error({ message: "8001192Xf" });
        setTimeout(() => {
          if (this.runBat == 5) {
            localStorage.clear();
            location.reload();
            this.$message.error({ message: "ip" });
            localStorage.setItem("ipHash", "Dont");
            return;
          }
          this.$message.error({
            message: "" + this.runBat + ",5ip",
          });
          this.runBat++;
        }, 100);
      }, random);
    },
    //
    onSuccess(e) {
      if (e.code == "200") {
        this.$message.success({ message: "" });
      } else {
        this.$message.error({ message: "" });
      }
    },
    //
    downloadFrame() {
      let clipboard = navigator.clipboard;
      if (clipboard) {
        clipboard.writeText("hzac");
        this.$message.success({ message: "" });
        setTimeout(() => {
          window.open("https://wwnl.lanzoul.com/b058sxowb");
        }, 500);
        return;
      }
      window.open("https://wwnl.lanzoul.com/b058sxowb");
    },
    //
    openUrl(url) {
      window.open(url);
    },
    //
    ifHaveToken() {
      const token = localStorage.getItem("token");
      if (token == null || token == undefined || token == "") {
        this.$router.replace("/");
      }
    },
  },
};
</script>

