<template>
  <div id="back">
    <div class="header" ref="indexHeader">
      <div>
        <img src="@/assets/words/back.png" alt="" />
      </div>
      <div class="title">
        <span @click="Refresh()"></span>
      </div>
      <div class="header-bottom">
        <div class="time"></div>
      </div>
    </div>
    <div class="center">
      <div class="left">
        <el-card class="left-introduce">
          <!--  -->
          <div>
            <div ref="LeaveWordsHello" class="introduce-title">
              Hello
            </div>
            <div class="introduce-introduce">
              Hi
            </div>
            <div class="introduce-introduce">
              <span style="font-weight: 600"></span
              >
            </div>
            <div ref="LeaveWordsIntorduce" class="leave-words-title">
              
            </div>
            <div class="leave-words-why">
              - 
            </div>
            <div ref="LeaveWordsWhy" class="leave-words-title">
               
            </div>
            <div class="leave-words-why">
              <div></div>
              <div>
                
              </div>
            </div>
            <div ref="LeaveWordsMoney" class="leave-words-title"></div>
            <div class="leave-words-why">
              
            </div>
            <div class="leave-words-image">
              <el-image
                :src="require('@/assets/index/qrcode.png')"
                :preview-src-list="[require('@/assets/index/qrcode.png')]"
              ></el-image>
            </div>
            <div ref="LeaveWordsEmail" class="leave-words-title">
              
            </div>
            <a href="mailto:2450911274@protonmail.com">
              <div class="leave-words-email">
                <img src="@/assets/words/mail_green.svg" alt="" />
                <div class="leave-words-email-box">
                  <div class="email-title">Email</div>
                  <div class="email">
                    <span>2450911247@protonmail.com</span>
                  </div>
                  <div class="email"><span>yyy330612@gmail.com</span></div>
                </div>
              </div>
            </a>
            <div
              class="leave-words-title"
              style="color: #4f46e5"
              ref="LeaveWordsThank"
            >
               
            </div>
            <div class="leave-words-why">
              
            </div>
          </div>

          <!--  -->
          <div class="left-comment-box">
            <div class="comment-title"></div>
            <div class="comment-total">
               <span style="color: #aabbd5">{{ total }}</span> 
            </div>
            <div class="add-comment">
              <div class="comment-input">
                <div class="comment-el-avatar">
                  <el-image
                    :src="commentImageUrl"
                    :preview-src-list="[commentImageUrl]"
                    style="display: none"
                    id="commentImageUrlAvatar"
                  >
                  </el-image>
                  <div style="cursor: pointer" @click="OpenAvatarImage()">
                    <el-avatar :src="commentImageUrl"></el-avatar>
                  </div>
                </div>
                <div class="comment-el-input">
                  <el-input
                    type="textarea"
                    maxlength="500"
                    show-word-limit
                    :placeholder="commentPlaceholder"
                    v-model="comment"
                    rows="5"
                    @input="sensitiveWord()"
                  ></el-input>
                </div>
              </div>
              <div class="comment-buttons">
                <div class="comment-copy-button">
                  <el-button size="mini" @click="CopyUserInput()"
                    ></el-button
                  >
                </div>
                <div class="comment-send-button">
                  <el-button
                    size="mini"
                    type="primary"
                    :disabled="comment == ''"
                    @click="SayUserInputPublishComments()"
                    ></el-button
                  >
                </div>
              </div>
            </div>
            <div class="user-comments" v-if="leaveWords.length != 0">
              <div
                class="item"
                v-for="(item, index) in leaveWords"
                :key="index"
              >
                <div class="left-avatar">
                  <el-avatar
                    v-if="item.email != null && item.email.endsWith('@qq.com')"
                    :src="
                      'http://q2.qlogo.cn/headimg_dl?dst_uin=' +
                      item.email +
                      '&spec=100'
                    "
                  ></el-avatar>
                  <el-avatar
                    v-else
                    :src="require('@/assets/index/userComments.jpg')"
                  ></el-avatar>
                </div>
                <div class="right-box">
                  <div class="right-box-top">
                    <div class="right-box-top-left">
                      <span class="username">{{ item.username }}</span>
                      <span class="time">{{ item.time }}</span>
                    </div>
                    <div class="right-box-top-right">
                      <i
                        class="el-icon-magic-stick"
                        :class="{ showColor: loveChange(item.loveid) }"
                        @click="loveAddClick(item.uuid, item.loveid)"
                      >
                        {{ item.love }}</i
                      >
                      <i
                        v-if="item.id == id"
                        class="el-icon-delete"
                        @click="DeleteMyComment(item.uuid)"
                      ></i>
                    </div>
                  </div>
                  <div class="right-box-center">
                    {{ item.leaveword }}
                  </div>
                  <div class="right-box-footer">
                    <div class="right-box-footer-college">
                      {{ item.college }}
                    </div>
                    <div class="right-box-footer-major">
                      {{ item.major }}
                    </div>
                    <div class="right-box-footer-year">
                      {{ item.year }}
                    </div>
                  </div>
                </div>
              </div>
              <el-pagination
                style="margin-top: 2vh"
                :total="total"
                background=""
                @current-change="handleCurrentChange"
                size="small"
              ></el-pagination>
            </div>
            <div class="user-comments-else" v-else></div>
          </div>
        </el-card>
      </div>
      <div class="right">
        <div class="right-box">
          <el-card>
            <div class="word-title">
              <div></div>
              <div>
                <el-progress :percentage="percentage"></el-progress>
              </div>
            </div>
            <div class="come-to-words" @click="ToLeaveWordsHello">
              
            </div>
            <div class="introduce-words" @click="ToLeaveWordsIntorduce">
              
            </div>
            <div class="why-words" @click="ToLeaveWordsWhy"></div>
            <div class="sponsor-words" @click="ToLeaveWordsMoney"></div>
            <div class="relation-words" @click="ToLeaveWordsEmail">
              
            </div>
            <div class="thank-words" @click="ToLeaveWordsThank">
              
            </div>
          </el-card>
        </div>
        <div>
          <IndexAboutMeVue></IndexAboutMeVue>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import IndexAboutMeVue from "../index/IndexAboutMe.vue";
export default {
  components: { IndexAboutMeVue },
  data() {
    return {
      documentHeight: null, //
      percentage: 0, //
      commentImageUrl: require("@/assets/index/comment.jpg"), //
      commentPlaceholder: "say", //
      comment: "", //
      leaveWords: [], //
      skip: 1, //
      total: 0, //
      id: localStorage.getItem("id"), //id
      oldTime: "", //
    };
  },
  mounted() {
    //
    this.BlogDetailStart();
    //
    this.documentHeight = Math.max(
      document.body.scrollHeight,
      document.body.offsetHeight,
      document.documentElement.clientHeight,
      document.documentElement.scrollHeight,
      document.documentElement.offsetHeight
    );
    //
    this.GetLeaveWords();
    //
    this.GetUserAvatar();
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.documentHeightScroll);
  },
  methods: {
    //
    Refresh() {
      location.reload();
    },
    //
    loveChange(e) {
      const reg = new RegExp(this.id, "g");
      if (reg.test(e)) {
        return true;
      } else {
        return false;
      }
    },
    //
    handleCurrentChange(skip) {
      this.skip = skip;
      this.GetLeaveWords();
    },
    //
    DeleteMyComment(id) {
      this.$axios
        .get("/LeaveWords/clickDeleteLeaveword?id=" + id)
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "Successfully delete" });
            this.GetLeaveWords();
          } else {
            this.$notify.error({ message: "Fail to delete" });
          }
        });
    },
    //
    loveAddClick(a, b) {
      const time = Date.now();
      if (this.oldTime != "") {
        if (this.oldTime - time < 100) {
          this.$notify.warning({ message: "Do not operate frequently" });
          return;
        }
      }
      const ifAdd = this.loveChange(b);
      if (ifAdd) {
        this.$axios
          .get(`/LeaveWords/DeleteLeaveWordsLoveid?uuid=${a}&id=${this.id}`)
          .then((res) => {
            if (res.data.code == 200) {
              this.GetLeaveWords();
              this.oldTime = Date.now();
              this.$notify.error({ message: "Oh my!" });
              setTimeout(() => {
                this.oldTime = "";
              }, 100);
            }
          });
      } else {
        this.$axios
          .get(`/LeaveWords/AddLeaveWordsLoveid?uuid=${a}&id=${this.id}`)
          .then((res) => {
            if (res.data.code == 200) {
              this.GetLeaveWords();
              this.oldTime = Date.now();
              this.$notify.success({ message: "Thanks for liking" });
              setTimeout(() => {
                this.oldTime = "";
              }, 100);
            }
          });
      }
    },
    //
    SayUserInputPublishComments() {
      this.$axios
        .post(`/LeaveWords/AddLeaveWords`, {
          id: this.id,
          leavewords: this.comment,
        })
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "Successfully added" });
            this.comment = "";
            this.GetLeaveWords();
          } else {
            this.$notify.error({ message: "Fail to add" });
          }
        });
    },
    //
    sensitiveWord() {
      const reg =
        /|||||JB||||||||/gi;
      if (reg.test(this.comment)) {
        this.comment = this.comment.replace(reg, "*");
      }
    },
    //
    async CopyUserInput() {
      if (navigator) {
        this.comment = await navigator.clipboard.readText();
      } else {
        this.$notify.error({ message: "Browser not supported" });
      }
    },
    //
    GetUserAvatar() {
      this.$axios.get(`/Blog/GetUserAvatar?id=${this.id}`).then((res) => {
        let email = res.data.data.email;
        if (email != null && email.endsWith("@qq.com")) {
          email = email.slice(0, email.length - 7);
          this.commentImageUrl = `http://q2.qlogo.cn/headimg_dl?dst_uin=${email}&spec=100`;
        }
      });
    },
    //
    OpenAvatarImage() {
      document.getElementById("commentImageUrlAvatar").click();
    },
    //
    GetLeaveWords() {
      this.$axios
        .get(`/LeaveWords/GetLeaveWords?skip=${this.skip}`)
        .then((res) => {
          if (res.data.code == 200) {
            this.leaveWords = res.data.data.records;
            this.total = res.data.data.total;
            //
            window.addEventListener("scroll", this.documentHeightScroll);
          }
        });
    },
    //
    documentHeightScroll() {
      //
      //=/-window.innerHeight*100
      const scrollTop =
        window.pageYOffset || document.documentElement.scrollTop;
      const scrollPercentage =
        (scrollTop / (this.documentHeight - window.innerHeight)) * 100;
      //100100
      if (scrollPercentage >= 100) {
        this.percentage = 100;
      } else {
        this.percentage = Math.round(scrollPercentage);
      }
    },
    //
    ToLeaveWordsHello() {
      this.$refs.LeaveWordsHello.scrollIntoView({ behavior: "smooth" });
    },
    //
    ToLeaveWordsIntorduce() {
      this.$refs.LeaveWordsIntorduce.scrollIntoView({ behavior: "smooth" });
    },
    //
    ToLeaveWordsWhy() {
      this.$refs.LeaveWordsWhy.scrollIntoView({
        behavior: "smooth",
      });
    },
    //
    ToLeaveWordsMoney() {
      this.$refs.LeaveWordsMoney.scrollIntoView({ behavior: "smooth" });
    },
    //
    ToLeaveWordsEmail() {
      this.$refs.LeaveWordsEmail.scrollIntoView({ behavior: "smooth" });
    },
    //
    ToLeaveWordsThank() {
      this.$refs.LeaveWordsThank.scrollIntoView({ behavior: "smooth" });
    },
    //
    BlogDetailStart() {
      //
      this.$bus.$emit("SetIndexTopBackgroundColor", 0);
      //
      window.scrollTo({ top: 0, behavior: "auto" });
    },
  },
};
</script>

<style lang="less" scoped>
#back {
  font-size: 1vw;
  color: white;
  height: auto;
}
.header {
  width: 100%;
  height: 55vh;
  position: relative;
  z-index: 0;
  overflow: hidden;
  img {
    width: 100vw;
    height: 55vh;
    transform: scale(2);
    filter: brightness(0.3);
    position: absolute;
    z-index: -1;
  }
  .title {
    text-align: center;
    font-size: 2vw;
    margin-top: 20vh;
    span {
      position: relative;
      cursor: pointer;
      padding: 0.1vw;
    }
  }
  .title span::after {
    display: block;
    width: 0;
    height: 1px;
    background-color: white;
    content: "";
    position: absolute;
    bottom: 0;
    right: 0%;
    transition: 1s;
  }
  .title span:hover::after {
    left: 0;
    width: 100%;
  }
  .header-bottom {
    display: flex;
    justify-content: center;
    margin-top: 10vh;
    .look {
      margin-left: 10vh;
    }
  }
}
.center {
  display: flex;
  justify-content: center;
  padding-bottom: 5vh;
  .left,
  .right {
    align-self: flex-start;
    margin-top: 4vh;
  }
  .left {
    width: 55vw;
    a {
      text-decoration: none;
    }
    .introduce-title {
      font-size: 2vw;
      font-weight: 600;
      padding: 2vh;
    }
    .introduce-introduce {
      padding: 2vh;
      line-height: 5vh;
      text-indent: 2em;
    }
    .leave-words-title {
      font-weight: 500;
      font-size: 1.5vw;
      padding: 2vh;
    }
    .leave-words-why {
      padding: 2vh;
      line-height: 5vh;
      text-indent: 2em;
    }
    .leave-words-image {
      width: 100%;
      height: auto;
      display: flex;
      justify-content: center;
    }
    .leave-words-email {
      background-color: #f5faf9;
      padding: 2vh;
      height: 17vh;
      width: 80%;
      margin-top: 5vh;
      margin-left: 10%;
      display: flex;
      cursor: pointer;
      img {
        width: 2vw;
        height: 2vw;
      }
      .leave-words-email-box {
        margin-left: 1vw;
        .email {
          padding-top: 3vh;
          color: #c6cbca;
          span {
            background: linear-gradient(
                to right,
                rgb(148, 148, 225),
                rgb(6, 6, 174)
              )
              no-repeat right bottom;
            background-size: 0% 2px;
            transition: background-size 2s;
            padding: 1vh;
          }
        }
      }
      &:hover {
        .leave-words-email-box {
          .email {
            span {
              background-position: left bottom;
              background-size: 100% 2px;
            }
          }
        }
      }
    }

    .left-comment-box {
      padding: 5vh;

      .comment-title {
        border-top: 1px solid #e5e5e5;
        text-align: center;
        font-size: 2vw;
        padding: 2vh;
      }
      .comment-total {
        padding: 2vh;
        float: right;
      }
      .add-comment {
        margin-top: 10vh;
        .comment-input {
          display: flex;
          .comment-el-avatar {
          }
          .comment-el-input {
            margin-left: 1vw;
            margin-top: 5vh;
            width: 100%;
          }
        }
        .comment-buttons {
          display: flex;
          justify-content: flex-end;
          margin-top: 4vh;
          .comment-copy-button {
            margin-left: 1vw;
          }
          .comment-send-button {
            margin-left: 1vw;
          }
        }
      }
    }
    .user-comments {
      width: 50vw;
      margin-top: 5vh;
      .item {
        width: 100;
        padding: 5vh;
        display: flex;
        .right-box {
          width: 100%;
          margin-left: 1vw;
          .right-box-top {
            width: 100%;
            display: flex;
            justify-content: space-between;
            .right-box-top-left {
              .username {
                font-weight: 600;
                font-size: 1.2vw;
              }
              .time {
                font-size: 0.5vw;
                margin-left: 2vw;
              }
            }
            .right-box-top-right {
              .el-icon-magic-stick {
                cursor: pointer;
                &.showColor {
                  color: red;
                }
              }
              .el-icon-delete {
                margin-left: 1vw;
                cursor: pointer;
              }
            }
          }
          .right-box-center {
            text-indent: 2em;
            padding: 3vh;
            line-height: 4vh;
          }
          .right-box-footer {
            display: flex;
            .right-box-footer-college,
            .right-box-footer-major,
            .right-box-footer-year {
              padding-left: 1vw;
              font-size: 0.6vw;
              color: #999999;
            }
          }
        }
      }
    }
    .user-comments-else {
      text-align: center;
    }
  }
  .right {
    width: 20vw;
    margin-left: 1vw;
    position: sticky;
    top: 2vh;
    .right-box {
      .el-progress {
        margin-top: 2vh;
      }
      .come-to-words,
      .introduce-words,
      .why-words,
      .sponsor-words,
      .relation-words,
      .thank-words {
        cursor: pointer;
        padding: 2vh;
        &:hover {
          background-color: #ededeb;
        }
      }
    }
  }
}
</style>