<template>
  <div>
    <div class="" v-if="shares.length != 0">
      <div class="item" v-for="(item, index) in shares" :key="index">
        <el-card>
          <div class="title" @click="OnopenNewHref(item.content)">
            {{ item.content }}
          </div>
          <div class="content">{{ item.title }}</div>
          <div class="love">
            <i class="el-icon-magic-stick"> {{ item.love }}</i>

            <el-popconfirm
              title=""
              @confirm="DeleteShareUrlCollector(item.uuid)"
            >
              <i class="el-icon-delete" slot="reference"> </i>
            </el-popconfirm>
          </div>
        </el-card>
      </div>
      <el-pagination
        background=""
        size="small"
        :total="total"
        @current-change="handeChange"
      ></el-pagination>
    </div>
    <div class="item-else" v-else>
      <el-card>  </el-card>
    </div>
    <el-dialog :visible.sync="visible" title="">
      <el-form label-width="auto">
        <el-form-item label="">
          <el-input v-model="form.content" style="80%">
            <el-button @click="clickCopy()" slot="append"></el-button>
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="form.title"
            @input="sensitiveWord()"
            type="textarea"
            rows="10"
            show-limit-word
            maxlength="200"
          >
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.classify">
            <el-option value=""></el-option>
            <el-option value=""></el-option>
            <el-option value=""></el-option>
            <el-option value=""></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div style="display: flex; justify-content: space-around">
        <el-button @click="Reset()"></el-button>
        <el-button @click="AddShareUrl()" type="primary"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: localStorage.getItem("id"), //id
      shares: [], //
      skip: 1, //
      total: 0, //
      form: {
        title: "",
        content: "",
        classify: "",
        id: localStorage.getItem("id"),
      }, //
      visible: false, //
    };
  },
  mounted() {
    //
    this.MyShareUrlCollector();
    //
    this.$bus.$on("ShowPublishLeaveWords", () => {
      this.visible = true;
    });
  },
  methods: {
    //
    AddShareUrl() {
      if (
        this.form.title == "" ||
        this.form.content == "" ||
        this.form.classify == ""
      ) {
        this.$notify.error({ message: "Please input" });
        return;
      }
      const reg1 = /^https?:\/\//gi;
      const reg2 = /^www\./gi;
      if (!reg1.test(this.form.content) && !reg2.test(this.form.content)) {
        this.$notify.error({ message: "Please enter the correct URL" });
        return;
      }
      this.$axios.post("/ShareUrl/AddShareUrl", this.form).then((res) => {
        if (res.data.code == 200) {
          this.visible = false;
          this.Reset();
          this.$notify.success({ message: "Release success" });
        }
      });
    },
    //
    async clickCopy() {
      //
      if (navigator) {
        this.form.content = await navigator.clipboard.readText();
      } else {
        this.$notify.error({ message: "The clipboard is forbidden to open" });
      }
    },
    //
    sensitiveWord() {
      const reg =
        /|||||JB||||||||||||/gi;
      if (reg.test(this.form.title)) {
        this.form.title = this.form.title.replace(reg, "*");
      }
    },
    //
    Reset() {
      this.form.title = "";
      this.form.content = "";
      this.form.classify = "";
    },
    //
    handeChange(skip) {
      this.skip = skip;
      this.MyShareUrlCollector();
    },
    //
    DeleteShareUrlCollector(e) {
      this.$axios.get(`/ShareUrl/DeleteShareUrl?uuid=${e}`).then((res) => {
        if (res.data.code == 200) {
          this.$notify.success({ message: "Successfully delete" });
          this.MyShareUrlCollector();
        }
      });
    },
    //
    OnopenNewHref(e) {
      window.open(e);
    },
    //
    MyShareUrlCollector() {
      this.$axios
        .get(`/ShareUrl/GetMyUserShareUrl?id=${this.id}&skip=${this.skip}`)
        .then((res) => {
          if (res.data.code == 200) {
            this.total = res.data.data.total;
            this.shares = res.data.data.records;
          }
        });
    },
  },
};
</script>

<style lang="less" scoped>
.item {
  padding: 3vh;
  .el-card {
    .title {
      padding-top: 3vh;
      cursor: pointer;
    }
    .content {
      padding-top: 3vh;
    }
    .love {
      padding-top: 3vh;
      display: flex;
      justify-content: flex-end;
      .el-icon-magic-stick {
        color: red;
        cursor: pointer;
      }
      .el-icon-delete {
        cursor: pointer;
        margin-left: 1vw;
      }
    }
  }
}
.item-else {
  text-align: center;
  color: black;
}
</style>