<template>
  <div>
    <el-dialog
      :visible.sync="visible"
      :before-close="beforeClose"
      title=""
    >
      <el-form
        ref="AdminRightUserEditRef"
        label-width="100px"
        :model="form"
        :rules="rules"
      >
        <el-form-item label="" prop="username">
          <el-input
            v-model="form.username"
            clearable
            style="width: 80%"
          ></el-input>
        </el-form-item>

          <el-form-item label="" prop="password">
            <el-input
              v-model="form.password"
              clearable
              style="width: 80%"
            ></el-input>
        </el-form-item>
        <el-form-item label="" prop="college">
          <el-input
            v-model="form.college"
            clearable
            style="width: 80%"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="major">
          <el-input
            v-model="form.major"
            clearable
            style="width: 80%"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="year">
          <el-input v-model="form.year" clearable style="width: 80%"></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="form.status" clearable>
            <el-option value=""></el-option>
            <el-option value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="form.email"
            clearable
            style="width: 80%"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="form.phone"
            clearable
            style="width: 80%"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="form.wechat"
            clearable
            style="width: 80%"
          ></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex; justify-content: space-around">
        <el-button @click="beforeClose"></el-button>
        <el-button type="primary" @click="submit('AdminRightUserEditRef')"
          ></el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        id: "",
        username: "",
        status: "",
        college: "",
        major: "",
        year: "",
        email: "",
        phone: "",
        wechat: "",
      },//
      rules: {
        username: [{ required: true, message: "" }],
        status: [{ required: true, message: "" }],
        college: [{ required: true, message: "" }],
        major: [{ required: true, message: "" }],
        year: [{ required: true, message: "" }],
      },//
      visible: true,
      id:"",//id
    };
  },
  mounted() {
    //
    this.id = this.$route.query.id;
    this.$axios.get("/User/AdminSelectUserList?id=" + this.id).then(
      (res) => {
        if (res.data.code == 200) {
          this.form = res.data.data;
          if (this.form.status == "0") {
            this.form.status = "";
          } else {
            this.form.status = "";
          }
        }
      }
    );
  },
  methods: {
    //
    submit(formdata) {
      this.$refs[formdata].validate((e) => {
        if (e) {
          this.AdminChangeUserInfo();
        } else {
          this.$notify.warning({ message: "" });
        }
      });
    },
    //
    beforeClose() {
      this.$router.back();
    },
    //
    AdminChangeUserInfo() {
      if (
        this.id == "" ||
        this.username == "" ||
        this.password == "" ||
        this.college == "" ||
        this.major == "" ||
        this.year == ""
      ) {
        this.$notify.warning({ message: "" });
      }
      if (this.form.status == "") {
        this.form.status = "0";
      } else {
        this.form.status = "1";
      }
      this.$axios.post("/User/AdminChangeUserInfo", this.form).then(
        (res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "" });
            this.beforeClose();
            this.$bus.$emit("AdminOff");
          } else {
            this.$notify.error({ message: "" });
          }
        }
      );
    },
  },
};
</script>

<style lang="less" scoped>
</style>