<template>
  <div id="back">
    <div class="header" ref="indexHeader">
      <div>
        <img src="@/assets/competition/back.jpg" alt="" />
      </div>
      <div class="title">
        <span @click="Refresh()"></span>
      </div>
      <div class="header-bottom">
        <div class="time"></div>
        <div class="look" title="ip">{{ ip }}</div>
      </div>
    </div>
    <div class="center">
      <div class="left">
        <el-card class="introduce" shadow>
          <div class="competition-title"></div>
          <div class="competition-text">
            
          </div>
          <div>
            <div class="competition-target"></div>
            <div class="competition-text">
              
            </div>
          </div>
          <div>
            <div class="competition-type"></div>
            <div class="competition-text">
              
            </div>
            <div class="competition-text">
              
            </div>
          </div>
          <div>
            <div class="competition-technology"></div>
            <div class="competition-text"></div>
            <div class="competition-text"></div>
          </div>
          <div>
            <div class="competition-prize"></div>
            <div class="competition-text"></div>
            <div class="competition-text"></div>
          </div>
          <div>
            <div class="competition-result"></div>
            <div class="competition-text">
              
            </div>
          </div>
        </el-card>
        <el-card class="left-bottom-card">
          <div style="display: flex">
            <div class="left-bottom-title">
              <span style="font-size: 1vw">()</span>
            </div>
            <img src="@/assets/competition/map.png" alt="" />
          </div>

          <div v-if="rankGame.length != 0">
            <div
              class="bottom-rank"
              v-for="(item, index) in rankGame"
              :key="index"
            >
              <span :class="index < 3 ? 'good' : ''">{{ index + 1 }}</span>
              {{ item.major }}{{ item.year }}{{ item.username }}
              <span>{{ item.score }}</span>
            </div>
          </div>
          <div v-else style="text-align: center; padding: 3vh"></div>
        </el-card>
        <el-card class="right-bottom-card">
          <div class="right-bottom-title">
            <span></span>
            <span class="cooperation" @click="CompetitionCooperation"
              ></span
            >
          </div>
          <div v-if="lastGame.length != 0">
            <el-descriptions
              :colon="false"
              style="margin-top: 2vh; font-size: 1vw"
            >
              <el-descriptions-item label=""></el-descriptions-item>
              <el-descriptions-item label=""></el-descriptions-item>
              <el-descriptions-item label=""></el-descriptions-item>
            </el-descriptions>
            <el-descriptions
              :colon="false"
              v-for="(item, index) in 1"
              :key="index"
              style="margin-top: 2vh; font-size: 1vw"
            >
              <el-descriptions-item label=""></el-descriptions-item>
              <el-descriptions-item label="2024-4-30 "></el-descriptions-item>
              <el-descriptions-item label=""></el-descriptions-item>
            </el-descriptions>
          </div>
          <div v-else style="text-align: center; padding: 3vh"></div>
        </el-card>
      </div>
      <div class="right">
        <el-card shadow>
          <div class="right-prize"></div>
          <div class="right-point-race" @click="ToPointRace"></div>
          <div class="right-apply"></div>
          <div class="right-apply" @click="ToCompetitionTopic()"></div>
        </el-card>
        <div>
          <IndexAboutMeVue></IndexAboutMeVue>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import IndexAboutMeVue from '../index/IndexAboutMe.vue';
export default {
  components: {IndexAboutMeVue},
  data() {
    return {
      ip: "", //ip
      rankGame: [], //
      lastGame: [1], //
    };
  },
  mounted() {
    //ip
    this.GetUserIp();
    //
    this.BlogDetailStart();
    //
    this.GetCompetitionRanking();
  },
  beforeDestroy() {
    //
  },
  methods: {
    //
    ToPointRace() {
      this.$router.replace("/point-race");
    },
    //
    CompetitionCooperation() {
      let id = localStorage.getItem("id");
      this.$axios.get(`/question/IfQuestionAdmin?userId=${id}`).then((res) => {
        if (res.data.code == 200) {
          this.$router.push("/question/");
        } else {
          let password = prompt("");
          this.$axios
            .get(`/question/AddQuestionAdmin?key=${password}&userId=${id}`)
            .then((res) => {
              if (res.data.code == 200) {
                this.$router.push("/question/");
              } else {
                this.$notify.error({ message: "" });
              }
            });
        }
      });
    },
    //
    GetCompetitionRanking() {
      this.$axios.get(`/question/GetCompetitionRanking`).then((res) => {
        this.rankGame = res.data.data;
      });
    },
    //
    ToCompetitionTopic() {
      this.$router.push("/topic");
    },
    //
    BlogDetailStart() {
      //
      this.$bus.$emit("SetIndexTopBackgroundColor", 0);
      //
      window.scrollTo({ top: 0, behavior: "auto" });
    },
    //
    Refresh() {
      location.reload();
    },
    //ip
    GetUserIp() {
      this.$axios.get("/question/GetUserIp").then((res) => {
        this.ip = res.data.data;
      });
    },
  },
};
</script>

<style lang="less" scoped>
#back {
  font-size: 1vw;
  color: white;
  height: auto;
}
.header {
  width: 100%;
  height: 55vh;
  position: relative;
  z-index: 0;
  overflow: hidden;
  img {
    width: 100vw;
    height: 55vh;
    filter: brightness(0.3);
    position: absolute;
    z-index: -1;
  }
  .title {
    text-align: center;
    font-size: 2vw;
    margin-top: 20vh;
    span {
      position: relative;
      cursor: pointer;
      padding: 0.1vw;
    }
  }
  .title span::after {
    display: block;
    width: 0;
    height: 1px;
    background-color: white;
    content: "";
    position: absolute;
    bottom: 0;
    right: 0%;
    transition: 1s;
  }
  .title span:hover::after {
    left: 0;
    width: 100%;
  }
  .header-bottom {
    display: flex;
    justify-content: center;
    margin-top: 10vh;
    .look {
      margin-left: 10vh;
    }
  }
}
.center {
  display: flex;
  justify-content: center;
  padding-bottom: 5vh;
  .left,
  .right {
    align-self: flex-start;
    margin-top: 4vh;
  }
  .left {
    width: 55vw;
    .introduce {
      .competition-title {
        padding: 1vh;
        font-size: 2vw;
        font-weight: 600;
      }
      .competition-text {
        line-height: 4vh;
        padding: 2vh;
        text-indent: 2em;
      }
      .competition-target,
      .competition-type,
      .competition-technology,
      .competition-prize,
      .competition-result {
        font-size: 1.5vw;
        padding: 2vh;
        font-weight: 600;
      }
    }
    .left-bottom-card {
      margin-top: 2vh;
      img {
        width: 3vw;
        height: 2vw;
      }
      .left-bottom-title {
        font-weight: 600;
        font-size: 3vh;
        margin-bottom: 1vh;
      }
    }
    .right-bottom-card {
      border: none;
      margin-top: 3vh;
      position: relative;
      .right-bottom-title {
        font-size: 3vh;
        display: flex;
        font-weight: 600;
        justify-content: space-between;
        color: #595959;
        .cooperation {
          font-size: 1.5vh;
          color: blue;
          margin-top: 1.5vh;
          cursor: pointer;
        }
      }
      &:hover {
        box-shadow: 4px 4px 10px #c5c5c5;
      }
    }
    .bottom-rank {
      padding-top: 2vh;
      border-bottom: 1px solid rgba(0, 0, 0, 0.121);
      border-radius: 1vh;
      justify-content: space-around;
      display: flex;
      padding: 3vh;
      .good {
        color: red;
      }
      &:last-child {
        border-bottom: none;
      }
    }
  }
  .right {
    width: 20vw;
    position: sticky;
    top: 2vh;
    margin-left: 1vw;
    .el-card {
      border-radius: 5px;
      .right-apply,
      .right-point-race,
      .right-prize {
        display: flex;
        justify-content: center;
        padding: 2vh;
        cursor: pointer;
      }
    }
  }
}
</style>