import axios from "axios";
import router from "@/router";
import { Notification } from "element-ui";
import NProgress from "nprogress";
import "nprogress/nprogress.css";

//  ip
let isFrequencyWarningShown = false;

//axios
const server = axios.create({
  history: true,
  baseURL: "https://8.130.43.227:8085",
  // baseURL: "http://localhost:8085",
  // baseURL:"http://192.168.235.93:8085",
  withCredentials: true,
  timeout: 10000,
});

//
server.interceptors.request.use((config) => {
  // //
  NProgress.start();
  //
  setTimeout(() => {
    NProgress.done();
  }, 10000);
  //
  const token = localStorage.getItem("token");
  //null
  if (token != null) {
    config.headers.Authorization = "Bearer " + token;
  }
  return config;
});

//
server.interceptors.response.use(
  (res) => {
    //401
    if (res.data.code == "401") {
      if (router.currentRoute.path != "/C-login") {
        localStorage.removeItem("token");
        localStorage.removeItem("id");
        Notification.error({
          message: "Authentication failed. Please re-authenticate",
        });
        router.replace("/C-login");
        return res;
      }
    }
    //101
    if (res.data.code == "101" && !isFrequencyWarningShown) {
      isFrequencyWarningShown = true;
      Notification.warning({
        message: "The number of requests is too frequent. Please wait",
      });
      setTimeout(() => {
        isFrequencyWarningShown = false;
      }, 2000);
      return res;
    }
    //
    NProgress.done();
    return res;
  },
  (error) => {
    console.error("Axios Response Error:", error);
    Notification.error({ message: error.message }).catch(console.error);

    return Promise.reject(error);
  }
);

//
export default server;
