<template>
  <div>
    <div class="top">
      <div>
        <i class="el-icon-search"></i>
        <span> </span>
      </div>
    </div>
    <div class="center" style="margin-top: 2%">
      <el-table :data="user">
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column
          width="250"
          label=""
          prop="password"
        ></el-table-column>
        <el-table-column label="" prop="college"></el-table-column>
        <el-table-column label="" prop="major"></el-table-column>
        <el-table-column label="" prop="year"></el-table-column>
        <el-table-column label="" prop="">
          <template slot-scope="scope">
            <el-popconfirm
              title=""
              @confirm="AdminDeleteAdminUser(user[scope.$index].id)"
            >
              <el-button size="mini" slot="reference"></el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: [],//
      id: "",//id
    };
  },
  mounted() {
    this.GetAdminUserAll();
  },
  methods: {
    //
    AdminDeleteAdminUser(e) {
      this.$prompt("", "")
        .then((value) => {
          if (value == null) return;
          this.$axios
            .get(
              `/User/AdminDeleteAdminUser?adminpassword=${value.value}&id=${e}`
            )
            .then((res) => {
              if (res.data.code == 200) {
                this.GetAdminUserAll();
                this.$notify.success({ message: "" });
              } else {
                this.$notify.error({ message: "" });
              }
            });
        })
        .catch(() => {
          this.$notify.info({ message: "" });
        });
    },
    //
    GetAdminUserAll() {
      this.$axios.get("/User/GetAdminUserAll").then((res) => {
        if (res.data.code == 200) {
          this.user = res.data.data;
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
</style>