<template>
  <div>
    <div class="header">
      <div class="top" style="display: flex; justify-content: space-between">
        <div>
          <i class="el-icon-search"></i>
          <span> </span>
        </div>
        <div>
          <el-button
            size="mini"
            type="primary"
            icon="el-icon-search"
            @click="AdminSearchUser()"
            ></el-button
          >
          <el-button
            size="mini"
            type=""
            icon="el-icon-refresh"
            @click="Refresh()"
            ></el-button
          >
          <el-button
            size="mini"
            type="danger"
            plain
            icon="el-icon-delete"
            @click="AdminDeleteUserAll"
            ></el-button
          >
          <el-button
            size="mini"
            type="info"
            icon="el-icon-s-home"
            @click="ToAssociationIndex()"
            ></el-button
          >
        </div>
      </div>
      <div class="bottom" style="margin-top: 2%">
        <el-form style="margin-left: 2%" :inline="true">
          <el-form-item label="">
            <div @keyup.enter="AdminSearchUser()">
              <el-input size="small" v-model="searchUser"></el-input>
            </div>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="center" style="margin-top: 2%">
      <el-table
        :data="user"
        @selection-change="onSelectionChange"
        :header-cell-style="{ 'text-align': 'center' }"
      >
        <el-table-column type="selection"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="college"></el-table-column>
        <el-table-column label="" prop="major"></el-table-column>
        <el-table-column label="" prop="year"></el-table-column>
        <el-table-column label="" prop="status"></el-table-column>
        <el-table-column width="250" label="" prop="">
          <template slot-scope="scope">
            <div style="display: flex; justify-content: space-around">
              <el-button size="mini" @click="Edit(user[scope.$index].id)"
                ></el-button
              >
              <el-popconfirm
                title=""
                @confirm="AdminDeleteUser(user[scope.$index].id)"
              >
                <el-button size="mini" slot="reference"></el-button>
              </el-popconfirm>
              <el-button
                size="mini"
                @click="AddAdminUser(user[scope.$index].id)"
                ></el-button
              >
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="footer">
      <el-pagination
        :total="total"
        background=""
        @current-change="onCurrentChange"
      ></el-pagination>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchUser: "",//
      user: [],//
      pageNum: 1,//
      DeleteList: [],//
      id: "",//id
      total: 0,//
    };
  },
  mounted() {
    this.GetUserAll();
    this.$bus.$on("AdminOff", () => {
      this.GetUserAll();
    });
  },
  methods: {
    //
    onSelectionChange(e) {
      this.DeleteList = e;
    },
    //
    onCurrentChange(e) {
      this.pageNum = e;
      this.GetUserAll();
    },
    //
    Refresh() {
      this.searchUser = "";
      this.GetUserAll();
    },
    //
    ToAssociationIndex() {
      this.$router.replace("/index/home");
    },
    //
    AddAdminUser(e) {
      this.$prompt("", "")
        .then((value) => {
          if (value == null) return;
          this.$axios
            .get(`/User/AddAdminUser?adminpassword=${value.value}&id=${e}`)
            .then((res) => {
              if (res.data.code == 200) {
                this.$menotifyssage.success({ message: "" });
              } else {
                this.$mnotifyessage.error({ message: "" });
              }
            });
        })
        .catch(() => {
          this.$notify.info({ message: "" });
        });
    },
    //
    Edit(e) {
      this.$router.push({
        path: "/manage/users/edit",
        query: {
          id: e,
        },
      });
    },
    AdminDeleteUserAll() {
      if (this.DeleteList.length == 0) return;
      let arr = [];
      this.DeleteList.forEach((item) => {
        arr.push(item.id);
      });
      this.$confirm("", "")
        .then(() => {
          this.$axios.post("/User/AdminDeleteUserAll", arr).then((res) => {
            if (res.data.code == 200) {
              this.$notify.success({ message: "" });
              this.GetUserAll();
              this.DeleteList = [];
            } else {
              this.$notify.error({ message: "" });
              this.DeleteList = [];
            }
          });
        })
        .catch(() => {
          this.$notify.error({ message: "" });
        });
    },
    //
    AdminDeleteUser(e) {
      this.$confirm("", "")
        .then(() => {
          this.$axios.get("/User/AdminDeleteUser?id=" + e).then((res) => {
            if (res.data.code == 200) {
              this.GetUserAll();
              this.$notify.success({ message: "" });
            } else {
              this.$notify.error({
                message: "",
                title: "",
                position: "top-left",
              });
            }
          });
        })
        .catch(() => {
          this.$notify.error({ message: "" });
        });
    },
    //
    AdminSearchUser() {
      if (this.searchUser == "" || this.searchUser == "\\") return;
      this.GetUserAll();
    },
    //10
    GetUserAll() {
      this.$axios
        .get(
          `/User/AllUser?pageNum=${this.pageNum}&searchUser=${this.searchUser}`
        )
        .then((res) => {
          if (res.data.code == 200) {
            this.user = res.data.data.records;
            this.total = res.data.data.total;
            for (let i = 0; i < this.user.length; i++) {
              if (this.user[i].status == "0") {
                this.user[i].status = "";
              } else {
                this.user[i].status = "";
              }
            }
          }
        });
    },
  },
};
</script>

<style lang="less" scoped>
.header {
}
.center {
}
.footer {
  margin-top: 2%;
  display: flex;
  justify-content: flex-end;
}
</style>