<template>
  <div>
    <div v-show="params == ''">
      <div class="top" style="display: flex; justify-content: space-between">
        <div>
          <i class="el-icon-search"></i>
          <span> </span>
        </div>

        <div>
          <el-button
            size="mini"
            type="danger"
            plain
            icon="el-icon-plus"
            @click="ShowAddAdminSubject()"
            ></el-button
          >
        </div>
      </div>
      <div class="center" style="margin-top: 1%">
        <el-table :data="dataAdmin">
          <el-table-column label="" prop="id"></el-table-column>
          <el-table-column
            label=""
            prop="questionSubject"
          ></el-table-column>
          <el-table-column label="" prop="">
            <template slot-scope="scope">
              <el-popconfirm
                title=""
                @confirm="QuestionDeleteSubject(dataAdmin[scope.$index].id)"
              >
                <el-button size="mini" slot="reference"></el-button>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div v-show="params != ''">
      <div class="top" style="display: flex; justify-content: space-between">
        <div>
          <i class="el-icon-search"></i>
          <span> </span>
        </div>
        <div style="display: flex">
          <div style="margin-left: 1vw">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-download"
              @click="ExcelDown()"
              ></el-button
            >
          </div>
          <div style="margin-left: 1vw">
            <el-upload
              ref="uploadQuestion"
              :limit="1"
              :action="action + '/question/ExcelImport'"
              :on-change="onUploadChangeExcel"
              :auto-upload="false"
              :headers="headers"
              :file-list="fileList"
              :on-success="onUploadQuestionSuccess"
            >
              <el-button size="mini" type="" icon="el-icon-upload"
                ></el-button
              >
            </el-upload>
          </div>
          <div style="margin-left: 1vw">
            <el-button
              size="mini"
              type="danger"
              plain
              icon="el-icon-delete"
              @click="DeleteAllQuestionList"
              ></el-button
            >
          </div>
          <div style="margin-left: 1vw">
            <el-button
              size="mini"
              type="success"
              plain
              icon="el-icon-plus"
              @click="AddQuestionBank()"
              ></el-button
            >
          </div>
        </div>
      </div>
      <div class="center" style="margin-top: 2%">
        <el-table :data="dataSubject" @selection-change="onSelectionChange">
          <el-table-column type="selection"></el-table-column>
          <el-table-column label="" prop="id"></el-table-column>
          <el-table-column label="" prop="topic"></el-table-column>
          <el-table-column label="A" prop="a"></el-table-column>
          <el-table-column label="B" prop="b"></el-table-column>
          <el-table-column label="C" prop="c"></el-table-column>
          <el-table-column label="D" prop="d"></el-table-column>
          <el-table-column label="" prop="score"></el-table-column>
          <el-table-column width="200" label="" prop="">
            <template slot-scope="scope">
              <el-button
                size="mini"
                slot="reference"
                @click="GetQuestionOne(dataSubject[scope.$index].id)"
                ></el-button
              >
              <el-popconfirm
                title=""
                @confirm="DeleteQuestion(dataSubject[scope.$index].id)"
              >
                <el-button size="mini" slot="reference"></el-button>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div style="margin-top: 2%">
      <el-pagination
        v-show="total > 0 && params != ''"
        @current-change="onCurrentChange"
        :total="total"
      ></el-pagination>
    </div>

    <el-dialog title="" :visible="visible" :before-close="beforeClose">
      <el-form label-width="100px">
        <el-form-item label="">
          <el-input
            size="small"
            disabled
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input size="small" v-model="subjectType"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex; justify-content: flex-end">
        <el-button size="mini" type="" @click="beforeClose"></el-button>
        <el-button size="mini" type="primary" @click="AddAdminSubject"
          ></el-button
        >
      </div>
    </el-dialog>
    <el-dialog
      title=""
      :visible="visibleTopic"
      :before-close="beforeClose"
    >
      <el-form
        ref="AddQuestionBankRef"
        :rules="questionBankRules"
        :model="questionBank"
        label-width="100px"
      >
        <el-form-item label="">
          <el-input
            size="small"
            disabled
            v-model="questionBank.id"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item prop="topic" label="">
          <el-input
            type="textarea"
            size="small"
            v-model="questionBank.topic"
          ></el-input>
        </el-form-item>
        <el-form-item prop="a" label="A">
          <el-input
            type="textarea"
            size="small"
            v-model="questionBank.a"
          ></el-input>
        </el-form-item>
        <el-form-item prop="b" label="B">
          <el-input
            type="textarea"
            size="small"
            v-model="questionBank.b"
          ></el-input>
        </el-form-item>
        <el-form-item prop="c" label="C">
          <el-input
            type="textarea"
            size="small"
            v-model="questionBank.c"
          ></el-input>
        </el-form-item>
        <el-form-item prop="d" label="D">
          <el-input
            type="textarea"
            size="small"
            v-model="questionBank.d"
          ></el-input>
        </el-form-item>
        <el-form-item prop="rightAnswers" label="">
          <el-input
            type="textarea"
            size="small"
            v-model="questionBank.rightAnswers"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input size="small" v-model="params" disabled></el-input>
        </el-form-item>
        <el-form-item prop="score" label="">
          <el-select v-model="questionBank.score">
            <el-option value="1">1</el-option>
            <el-option value="1.5">1.5</el-option>
            <el-option value="2">2</el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div style="display: flex; justify-content: flex-end">
        <el-button size="mini" type="" @click="beforeClose"></el-button>
        <el-button size="mini" type="primary" @click="validateQuestionBank()"
          ></el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      params: "",
      dataAdmin: [],
      dataSubject: [],
      total: 0,
      pageNum: 1,
      visible: false,
      subjectType: "",
      questionBank: {
        id: null,
        topic: null,
        a: null,
        b: null,
        c: null,
        d: null,
        rightAnswers: null,
        score: "1",
        subjectType: null,
      },
      questionBankRules: {
        topic: [{ required: true, message: "" }],
        a: [{ required: true, message: "" }],
        b: [{ required: true, message: "" }],
        c: [{ required: true, message: "" }],
        d: [{ required: true, message: "" }],
        rightAnswers: [{ required: true, message: "" }],
        score: [{ required: true, message: "" }],
        subjectType: [{ required: true, message: "" }],
      },
      visibleTopic: false,
      DeleteQuestionList: [],
      action: this.$axios.defaults.baseURL,
      headers: {
        Authorization: "Bearer " + localStorage.getItem("token"),
      },
      fileList: [],
      successQuestionReload: false,
    };
  },
  mounted() {
    this.$bus.$on("CompetitionAdmin", () => {
      //
      setTimeout(() => {
        this.params = this.$route.params.subjectType;
        if (this.params == "") {
          this.GetSubjectType();
        } else {
          this.GetQuestionBank();
        }
      }, 0);
    });
    this.params = this.$route.params.subjectType;
    if (this.params == "") {
      this.GetSubjectType();
    } else {
      this.GetQuestionBank();
    }
  },
  methods: {
    //
    onUploadQuestionSuccess(e) {
      this.successQuestionReload = true;
      if (e.data == "") {
        this.$message.success({ message: "" });
        this.$bus.$emit("CompetitionAdmin");
        this.fileList = [];
        return;
      }
      this.$message.error({ message: "" });
    },
    //
    onUploadChangeExcel(file, fileList) {
      if (this.successQuestionReload) {
        this.successQuestionReload = false;
        return;
      }
      let fileListTemp = fileList[0];
      if (
        fileListTemp.raw.name != "template.xlsx" &&
        fileListTemp.name != "template.xlsx"
      ) {
        this.$message.error({ message: "template.xlsx" });
        this.fileList = [];
        return;
      }
      this.fileList.push(fileListTemp);
      this.ExcelImport();
    },
    //
    ExcelImport() {
      this.$confirm("", "Soft", { type: "warning" })
        .then(() => {
          this.$refs.uploadQuestion.submit();
        })
        .catch(() => {
          this.$message.info({ message: "" });
          this.fileList = [];
        });
    },

    //
    ExcelDown() {
      this.$axios
        .get("/question/ExcelDown", {
          responseType: "blob",
        })
        .then((res) => {
          let blob = new Blob([res.data], { type: res.data.type });
          let link = document.createElement("a");
          let url = window.URL.createObjectURL(blob);
          link.href = url;
          link.download = "template.xlsx";
          link.style.display = "none";
          document.body.appendChild(link);
          link.click();
          link.parentNode.removeChild(link);
          window.URL.revokeObjectURL(url);
        });
    },
    //
    AddQuestionBank() {
      let keys = Object.keys(this.questionBank);
      keys.forEach((item) => {
        this.questionBank[item] = null;
      });
      this.questionBank.score = "1";
      this.visibleTopic = true;
    },
    //
    GetQuestionOne(id) {
      this.$axios.get(`/question/GetQuestionOne?id=${id}`).then((res) => {
        if (res.data.code == 200) {
          this.questionBank = res.data.data;
          this.visibleTopic = true;
        } else {
          this.$notify.error({ message: "" });
        }
      });
    },
    //
    DeleteAllQuestionList() {
      if (this.DeleteQuestionList.length == 0) return;
      let arr = [];
      this.DeleteQuestionList.forEach((item) => {
        arr.push(item.id);
      });
      this.$axios.post(`/question/DeleteAllQuestionList`, arr).then((res) => {
        if (res.data.code == "1") {
          this.$message.success({ message: "" });
          location.reload();
        } else {
          this.$message.error({ message: "" });
        }
      });
    },
    //
    onSelectionChange(list) {
      this.DeleteQuestionList = list;
    },
    //
    onCurrentChange(pageNum) {
      this.pageNum = pageNum;
      this.$bus.$emit("CompetitionAdmin");
    },
    //
    DeleteQuestion(id) {
      this.$axios.get(`/question/DeleteQuestion?id=${id}`).then((res) => {
        if (res.data.code == 200) {
          this.$notify.success({ message: "" });
          this.$bus.$emit("CompetitionAdmin");
        } else {
          this.$notify.error({ message: "" });
        }
      });
    },
    //
    validateQuestionBank() {
      this.$refs.AddQuestionBankRef.validate((e) => {
        if (e) {
          this.AddQuestion();
        }
      });
    },
    //
    UpdateQuestion() {
      this.$axios
        .post(`/question/UpdateQuestion`, this.questionBank)
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "" });
            this.$bus.$emit("CompetitionAdmin");
            this.beforeClose();
          } else {
            this.$notify.error({ message: "" });
          }
        });
    },
    //
    AddQuestion() {
      if (this.questionBank.id != null) {
        this.UpdateQuestion();
        return;
      }
      if (
        this.questionBank.a == this.questionBank.b ||
        this.questionBank.a == this.questionBank.c ||
        this.questionBank.a == this.questionBank.d ||
        this.questionBank.b == this.questionBank.c ||
        this.questionBank.b == this.questionBank.d ||
        this.questionBank.c == this.questionBank.d
      ) {
        this.$notify.warning({ message: "" });
        return;
      }
      this.questionBank.subjectType = this.params;
      this.$axios
        .post(`/question/AddQuestion`, this.questionBank)
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "" });
            this.$bus.$emit("CompetitionAdmin");
            this.beforeClose();
          } else {
            this.$notify.error({ message: "" });
          }
        });
    },
    //
    QuestionDeleteSubject(id) {
      this.$axios
        .get(`/question/QuestionDeleteSubject?id=${id}`)
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "" });
            setTimeout(() => {
              this.$notify.warning({ message: "" });
            }, 500);
            this.$bus.$emit("CompetitionAdmin");
          } else {
            this.$notify.error({ message: "" });
          }
        });
    },
    //
    AddAdminSubject() {
      if (this.subjectType == "") {
        this.$message.warning({ message: "" });
        return;
      }
      this.$axios
        .get(`/question/AddAdminSubject?subjectType=${this.subjectType}`)
        .then((res) => {
          if (res.data.code == 200) {
            this.visible = false;
            this.$notify.success({ message: res.data.data });
            this.$bus.$emit("CompetitionAdmin");
          } else {
            this.$notify.error({ message: res.data.msg });
          }
          this.subjectType = "";
        });
    },
    //
    beforeClose() {
      this.visible = false;
      this.visibleTopic = false;
    },
    //
    ShowAddAdminSubject() {
      this.visible = true;
    },
    //
    GetQuestionBank() {
      this.$axios
        .get(
          `/question/QuestionList?subjectType=${this.params}&pageNum=${this.pageNum}`
        )
        .then((res) => {
          this.dataSubject = res.data.data.records;
          this.total = res.data.data.total;
        });
    },
    //
    GetSubjectType() {
      this.$axios.get("/question/GetSubjectType").then((res) => {
        this.dataAdmin = res.data.data;
      });
    },
  },
};
</script>

