<template>
  <div id="back">
    <IndexTopVue></IndexTopVue>
    <router-view ref="indexHeader"></router-view>
    <IndexBottomVue></IndexBottomVue>
    <AllBottomVue></AllBottomVue>
  </div>
</template>

<script>
import IndexTopVue from "@/components/public/IndexTop.vue";
import IndexBottomVue from "@/components/index/IndexBottom.vue";
import AllBottomVue from "@/components/index/AllBottom.vue";
export default {
  components: { IndexTopVue, IndexBottomVue, AllBottomVue },
  data() {
    return {
      backgroundColor: 0, //0  1  2
    };
  },
  mounted() {
    //
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    //
    IfLogin() {
      const id = localStorage.getItem("id");
      const token = localStorage.getItem("token");
      if (id == null || token == null) {
        this.$notify.error({ message: "Please log in" });
        this.$router.replace("/C-login");
      }
    },
    //
    handleScroll(e) {
      let indexHeaderHeight =
        this.$refs.indexHeader.$refs.indexHeader.scrollHeight;
      let scrollToTop = e.srcElement.scrollingElement.scrollTop;
      // //
      if (scrollToTop >= 800) {
        this.$bus.$emit("IndexArrowUpMessage", true);
      } else {
        this.$bus.$emit("IndexArrowUpMessage", false);
      }
      //
      if (scrollToTop < indexHeaderHeight - 100) {
        this.backgroundColor = 0;
      } else if (
        scrollToTop >= indexHeaderHeight - 100 &&
        scrollToTop <= indexHeaderHeight + 100
      ) {
        this.backgroundColor = 1;
      } else {
        this.backgroundColor = 2;
      }
      this.$bus.$emit("SetIndexTopBackgroundColor", this.backgroundColor);
    },
  },
};
</script>

<style scoped lang="less">
#back {
  width: 100vw;
  height: 100vh;
}
</style>
